body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fs {
    font-size: 1.2rem;
}

@media (min-width: 440px) {
    .fs {
        font-size: 2rem;
    }
}

.navbg {
    background-color: #232F3E;
    margin-bottom: 20px;
}
.form {
    flex-flow: nowrap;
}
.counter {
    height: 300px;
    width: 300px;
    
    margin: 0 auto;
    overflow: hidden;
}

.counter-display {
    height: 150px;
}

.counter-panel {
    height: 100px;
}

.btn {
    border-radius: 0 !important;
    cursor: pointer;
    box-shadow: none !important;
}

.counter-title {
    height: 50px;
    width: 300px;
    font-weight: 500;
    font-size: 20px;
    border: none;
}

.title-input {
    position: relative;
    text-align: center;
    padding: 10px 0;
}

.modify-input {
    margin-top: -20px;
    text-align: center;
    background: none;
    border: none;
    outline: 0;
}

.counterremove {
    display: flex;
    position: absolute;
    justify-content: flex-end;
    right: 10px;
    background: none;
    border: 0;
    box-shadow: none;
    margin: 0 -5px;
    padding: 0;
    cursor: pointer;
    color: gray;
}

.counterremove:hover, .countermodify:hover {
    color: #D44E4E;
}

.countermodify {
    display: flex;
    position: absolute;
    justify-content: flex-start;
    left: 10px;
    background: none;
    border: 0;
    box-shadow: none;
    margin: 0 -5px;
    padding: 0;
    cursor: pointer;
    color: gray;
}
